




















import {
  defineComponent, ref, Ref, reactive, computed,
} from '@vue/composition-api';
import axios from '@/api/axios';
import { getCurrentCompany } from '@/loaders/company';

const getMallTemplateConfig = async () => {
  const result = (await axios.get('/msgcenter/config', {
    params: {
      id: getCurrentCompany()?.id,
      type: 6,
    },
  })).data;
  // console.log('get config=======', result);
  return result || {};
};
const updateMallTemplateConfig = async (value) => {
  const result = await (axios.post('/msgcenter/config/change', {
    id: getCurrentCompany()?.id,
    type: 6,
    value,
  })).data;
  // console.log('update config=======', result);
  return result;
};

export default defineComponent({
  name: 'MallTemplate',
  components: {
  },
  props: {
  },
  setup(props, { emit }) {
    const config = ref({});
    (async function init() {
      config.value = await getMallTemplateConfig();
    }());
    function updateMode(mode) {
      config.value.mode = mode;
      updateMallTemplateConfig(config.value);
    }
    // updateMallTemplateConfig({
    //   mode: 'default',
    // });
    // getMallTemplateConfig();
    return {
      config,
      updateMode,
    };
  },
});

